<template>
	<div class="detail">
		<div class="title" v-for="">
			{{title}}
		</div>
		<div class="detail_box">
			<div class="left">
				<img :src="img"/>
			</div>
			<div class="right">
				<p>{{title}}</p>
				<p>{{showInfo}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		
		data(){
			return {
				img:'',
				title:"",
				showInfo:"",
				list:{}
			}
		},
		methods:{
			getData(){
				this.list=this.$route.query.list
				console.log(this)
				this.img=this.list.img;
				this.title=this.list.nm;
				this.showInfo=this.list.showInfo
//				this.$http.get("/my/ajax/movieOnInfoList?token=",{
//					movieId:this.id
//				}).then(res=>{
//					this.list_Info=res.data.movieList.filter((item)=>{
//						return item.id==this.id
//					}).filter(item=>{
//						return item.img = item.img.replace("w.h", "128.180")
//					})
//					//console.log(this.list_Info)
//				})
			}
		},
		created(){
			this.getData()
			
		},
		mounted(){
			
		}
	}
</script>

<style scoped>
	.detail .title{
		width: 100%;
		height: 51.5px;
		background: #DF2D2D;
		font-size: 18px;
		line-height: 51.5px;
		text-align: center;
		color: #fff;
		font-family: "微软雅黑";
		
		
	}
	.detail .detail_box{
		display: flex;
		box-sizing: border-box;
		width: 100%;
		height: 375px;
		height: 188px;
		padding: 19px 30px 19px 15px;
		background: #5b4e54;
	}
	.detail .detail_box .left{
		height: 110px;
		height: 150px;
	}
	.detail .detail_box .left img{
		height: 100%;
		height: 100%;
	}
</style>


